
.gw-container {
  position: relative;
  display: inline-block;
  vertical-align: middle;
  font-family: 'Open Sans', sans-serif;
  font-size: 12px;
  color: rgb(78, 78, 78);
}
.gw-container.w-120 {
  width: 120px;
}

.gw-container.w-400 {
  width: 400px;
}

.gw-container.ml-25 {
  margin-left: 25px;
}

.gw-container img.gw-icon {
  width: 100%;
}

.gw-container .gw-temperature {
  position: absolute;
  top: 50%;
  left: 50%;
  margin-left: -20px;
  margin-top: -10px;
  font-size: 20px;
  font-weight: bold;
}

.gw-container .gw-temperature-loading { font-weight: bold; }

.gw-container .gw-geo,
.gw-container .gw-precipitation,
.gw-container .gw-humidity,
.gw-container .gw-wind,
.gw-container .gw-outside { text-transform: uppercase; }
.gw-container .gw-outside * { vertical-align: middle; }
.gw-container .gw-outside img { width: 32px; }

.tab-container {
  margin-top: 20px;
  font-size: 16px;
}
.tab-container .tab {
  display: inline-block;
  padding: 0 25px;
}
.tab .tab-text {
  transition: all .2s ease-in-out;
  cursor: pointer;
}
.tab.current .tab-text { border-bottom: 3px solid #2870c6; }
.tab:first-child { padding-left: 0; }
.tab:last-child { padding-right: 0; }
.tab-container .tab-line {
  border-right: 1px solid #ebebeb;
}
.tab-container .tab-text {
  padding-bottom: 7px;
  border-bottom: 3px solid transparent;
}

.tabs-content { margin-top: 20px; }
.tab-content { display: none; }
.tab-content.current { display: block; }

.chart-container {
  position: relative;
}

.chart-container > .chart-container__tooltip {
  position: absolute;
  background-color: #D7D7D7;
  font-family: 'Open Sans', sans-serif;
  font-size: 12px;
  color: rgb(78, 78, 78);
  padding: 4px 10px;
  border-radius: 15px;
  font-weight: bold;
  visibility: hidden;
  opacity: 0;
  white-space: nowrap;
}

.chartminified-container {
  position: relative;
}
 
.chartminified-container > .chartminified-container__viewbox {
  position: absolute;
  left: 0;
  top: 0;
  width: 100px;
  background-color: #D7D7D7;
  opacity: .75;
  height: 100%;
  cursor: grab;
}

.chartminified-container > .chartminified-container__viewbox.grabbing,
body.grabbing { cursor: grabbing; }